import React from "react";
import Auth from '../utils/Auth';
import {
    Route,
    Redirect
} from 'react-router-dom';

const PrivateRoute = ({
    component: Component,
    ...rest
}) => ( <
    Route { ...rest
    }
    render = {
        props => (
            Auth.isUserAuthenticated() ? ( <
                Component { ...props
                } { ...rest
                }
                />
            ) : ( <
                Redirect to = {
                    {
                        pathname: '/',
                        state: {
                            from: props.location
                        }
                    }
                }
                />
            )
        )
    }
    />
)

const LoggedOutRoute = ({
    component: Component,
    ...rest
}) => ( <
    Route { ...rest
    }
    render = {
        props => (
            Auth.isUserAuthenticated() ? ( <
                Redirect to = {
                    {
                        pathname: '/dashboard',
                        state: {
                            from: props.location
                        }
                    }
                }
                />
            ) : ( <
                Component { ...props
                } { ...rest
                }
                />
            )
        )
    }
    />
)

const PropsRoute = ({
    component: Component,
    ...rest
}) => ( <
    Route { ...rest
    }
    render = {
        props => ( <
            Component { ...props
            } { ...rest
            }
            />
        )
    }
    />
)

export {
    PrivateRoute,
    LoggedOutRoute,
    PropsRoute
};


